import React from "react";
import { Link, graphql } from "gatsby";
import { rhythm, scale } from "../utils/typography";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTags } from '@fortawesome/free-solid-svg-icons';

import Bio from "../components/bio";
import Layout from "../components/layout";
import SEO from "../components/seo";
import { Tag } from "../components/index_components";

const TagsPageTemplate = ({ data, pageContext, location }) => {
  const { tagList, } = pageContext;
  const siteTitle = data.site.siteMetadata.title;

  return (
    <Layout location={location} title={siteTitle}>
      <SEO title={"Tags"} />
      <Bio />
      <h2
        style={{
          marginTop: rhythm(1),
          marginBottom: rhythm(1),
        }}
      >
        <FontAwesomeIcon icon={faTags} style={{marginRight: "10px"}}/>
        Tags
      </h2>
      {tagList.map((tag, i) => <Tag tag={tag} key={i}/>)}
    </Layout>
  );
};

export default TagsPageTemplate;

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
  }
`;
